html {
  font-size: 62.5%;
  @include rmin(1600) {
    font-size: 0.625vw; } }

body {
  background: #FFF;
  color: black;
  line-height: 1.4;
  min-width: 320px;
  @include f(l, 16);
  @include smooth-font; }

a {
  text-decoration: none;
  color: $color-blue;
  @include trans(color);
  &:hover,
  &:focus {
    color: red; } }

.lnk,
a:not([class]) {
  text-decoration: none;
  border-bottom: 1px solid rgba($color-blue, 0.3);
  @include trans();
  transition-property: color, border-color;
  &:hover {
    border-color: rgba(red, 0.3); } }

.lnk_black {
  &:not(:hover) {
    border-color: rgba(black, 0.3); } }

.out {
  overflow: hidden; }

.text {
  @include f(l, 16);
  line-height: 1.5625;
  p {
    margin: 1em 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  a {
    &:not(:hover) {
      color: inherit; } } }

.header {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  min-width: 320px;
  transition: box-shadow 0.3s ease;
  .hero-links {
    margin: 0;
    position: absolute;
    top: 0;
    height: 100%;
    left: 0;
    right: 0;
    opacity: 0;
    background: white;
    visibility: hidden;
    transition: all 0.2s ease-out;
    overflow: hidden;
    &:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 100%;
      width: 0; } }
  .hero-btn {
    transform: translateY(100%);
    transition: all 0.2s ease-out; }
  &.has-shadow {
    box-shadow: 0 0 10px 0 rgba(black, 0.3); }
  &.is-show-buttons {
    .hero-links {
      opacity: 1;
      visibility: visible; }
    .hero-btn {
      transform: translateY(0); }
    .lang {
      z-index: 0; } }
  &.is-hide-buttons {
    .hero-links {
      opacity: 0;
      visibility: hidden;
      transition-timing-function: ease-in; }
    .hero-btn {
      transform: translateY(-100%);
      transition-timing-function: ease-in; } }
  @include sm {
    .header__inner {
      padding: 15px 0;
      position: static; }
    .hero-links {
      padding-left: 15px;
      padding-right: 15px; }
    .lang {
      top: 50%;
      margin-top: -0.65em;
      right: 70px; }
    .nav {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      z-index: 3;
      top: 0;
      right: 0;
      left: 0;
      background: white;
      text-align: center;
      padding: 40px 15px 15px;
      box-shadow: 0 0 10px 0 rgba(black, 0.3);
      transition: 0.3s ease;
      transition-property: opacity, visibility;
      max-height: 100vh;
      overflow: auto;
      box-sizing: border-box;
      @include smooth-scroll; }
    .nav__item {
      display: block;
      margin: 0; }
    .nav__link {
      line-height: 2; }
    &.is-show-menu {
      .logo {
        z-index: 5; }
      .nav {
        opacity: 1;
        visibility: visible; } } } }

.header__inner {
  position: relative;
  padding: 22px 0; }

.header__buttons {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.menu-button {
  display: none;
  position: absolute;
  z-index: 5;
  top: 50%;
  right: 8px;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  padding: 8px;
  color: $color-blue;
  background: none;
  &:before,
  &:after,
  > span {
    content: "";
    display: block;
    width: 100%;
    border-top: 3px solid;
    transition: all 0.3s ease; }
  > span {
    margin: 6px 0; }
  .is-show-menu & {
    &:before {
      transform: rotate(45deg);
      transform-origin: 2px 6px; }
    &:after {
      transform: rotate(-45deg);
      transform-origin: 1px -3px; }
    > span {
      opacity: 0;
      transform: scale(0); } }
  &:hover {
    color: red; }
  @include sm {
    display: block; } }

.lang {
  position: absolute;
  z-index: 1;
  top: 25px;
  // right: -33px
  right: 0;
  @include f(l, 16);
  color: rgba(black, 0.3);
  a {
    border: none;
    &:not(:hover) {
      color: inherit; } }
  &:hover,
  &.is-hover {
    .lang__list {
      opacity: 1;
      visibility: visible; } }
  @include md {
    right: 0; } }

.lang__list {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  top: -10px;
  left: -15px;
  min-width: 100%;
  box-sizing: content-box;
  padding: 10px 15px;
  white-space: nowrap;
  background: white;
  box-shadow: 0 0 10px 0 rgba(black, 0.3);
  @include trans; }

.nav {
  position: relative; }

.nav__item {
  display: inline-block;
  vertical-align: top;
  margin-right: 20px;
  line-height: 1.7; }

.nav__link {
  display: inline-block;
  @include f(l, 16); }

.logo {
  display: inline-block;
  position: absolute;
  top: 50%;
  margin-top: -0.55em;
  right: 100%;
  margin-right: 75px;
  color: black;
  @include f(b, 16, 100);
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1;
  @include md {
    position: relative;
    top: auto;
    right: auto;
    margin: 0; } }

.link {
  color: $color-blue;
  text-decoration: none;
  border-bottom: 1px dotted;
  line-height: 1;
  cursor: pointer;
  @include trans(color);
  &:hover {
    color: red; } }

.toparea {
