.hero-figure {
  position: absolute;
  z-index: -1;
  top: -40px;
  right: 0;
  visibility: hidden;
  opacity: 0;
  // > svg:not(.drawsvg-animating)
  > svg {
    .dashed-line {
      stroke-dasharray: 3px !important;
      transition: stroke-dasharray 0.5s linear; } }
  &-iptv {
    .hero-figure__text {
      width: 330px; }
    // > svg
    //   width: 75rem
 }    //   height: 58rem
  &-ipcam {
