  top: 320px;
  right: 0;
  @include f(el, 14);
  line-height: 1.43;
  color: $color-blue; }

.hero-links {
  margin: 23px 0; }

.hero-btn {
  display: inline-block;
  margin-right: 15px;
  text-decoration: none;
  @include f(el, 16);
  color: inherit;
  @include trans(color);
  line-height: 1;
  color: $color-blue;
  cursor: pointer;
  user-select: none;
  > * {
    display: inline-block;
    vertical-align: middle; }
  span {
    border-bottom: 1px dotted; }
  g {
    stroke: currentColor; }
  circle {
    visibility: hidden;
    opacity: 0;
    @include trans; }
  &:hover {
    color: red; }
  &.is-active {
    color: black;
    cursor: default;
    span {
      border-color: rgba(black, 0); }
    circle {
      visibility: visible;
      opacity: 1; } } }


