  text-decoration: none;
  line-height: 1.375;
  @include trans(color);
  .svg-icon {
    display: block;
    font-size: 40px;
    font-size: 4rem;
    fill: currentColor;
    margin-bottom: 5px; }
  .lnk {
    @include trans(border-color); }
  &:hover {
    color: red;
    .lnk {
      border-color: rgba(red, 0.3); } } }

.social-link {
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  margin-right: 20px;
  min-width: 36px;
  text-align: center;
  @include trans(opacity);
  &:last-child {
    margin-right: 0; }
  .svg-icon {
    fill: currentColor; }
  .svg-facebook {
    font-size: 36px;
    font-size: 3.6rem; }
  .svg-twitter {
    font-size: 30px;
    font-size: 3rem; }
  .svg-youtube-play {
    font-size: 26px;
    font-size: 2.6rem; }
  &:hover {
    color: inherit;
    opacity: 0.8; } }

.hidden-text {
  @include hide-text; }

.map {
  height: 445px;
  height: 44.5rem;
  border: 1px solid rgba($color-blue, 0.3);
  border-width: 1px 0;
  @include sm {
    height: 250px; } }

.map__placeholder {
  height: 100%;
  background: img-url('map.jpg') no-repeat center;
  background-size: cover; }

.footer {
  padding: 15px 15px 20px;
  color: $color-blue;
  @include f(l, 12);
  line-height: 1.5;
  border: 1px solid rgba($color-blue, 0.3);
  @include clr;
  @include xs {
    text-align: center;
    .footer__left {
      float: none;
      margin-top: 10px; }
    .footer__right {
      float: none; } } }

.footer__left {
  float: left; }

.footer__right {
  float: right; }

.partners {
  padding: 29px 0 33px;
  text-align: center; }

.partners__title {
  margin-bottom: 16px;
  @include f(l, 12, 180);
  line-height: 1;
  text-transform: uppercase;
  color: $color-blue; }

.partners__item {
  display: inline-block;
  vertical-align: middle;
  opacity: 0.4;
  filter: grayscale(1);
  @include trans();
  &:not(:last-child) {
    margin-right: 25px; }
  &:hover {
    filter: grayscale(0);
    opacity: 1; }
  a {
    border: none; } }


.site-maker {
  @include clr;
  min-width: 278px;
  > img {
    float: left;
    margin: 4px 12px 0 0; }
  @include xs {
    > img {
      float: none;
      display: block;
      margin: 0 auto 10px; } } }

%dot {
  position: absolute;
  content: "";
  width: 4px;
  height: 4px;
  background: red; }

